import {tables, columns, defaultPage, monthDuration} from './config.json';
import {CreateCell, CreateCellNamed} from './createcell.js';

export class CreateTable {
    constructor(currentPage=defaultPage) {
        this.placeForStylize=document.querySelector('.allthereis');
        this.date=new Date();
        this.numOfRowsIndent=3; /* —Å—Ç–æ–ª—å–∫–æ —Å—Ç—Ä–æ–∫ –¥–æ–±–∞–≤–∏–º –Ω–∞ –∑–∞–≥–æ–ª–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã */
        this.IndentX=2; /* –æ–¥–∏–Ω —Å—Ç–æ–ª–±–µ—Ü –¥–ª—è –¥–∞—Ç—ã, –ø–æ—ç—Ç–æ–º—É –Ω–∞—á–∏–Ω–∞–µ–º —Å 2 */
        this.dateWidth=`15vh`; /* –∫–æ–ª–æ–Ω–∫–∞ —Å –¥–∞—Ç–∞–º–∏ */
        this.yearHeight=`4vh`; /* –∑–∞–≥–æ–ª–æ–≤–æ–∫ –≥–æ–¥–∞ */
        this.packetHeight=`4vh`; /* –∑–∞–≥–æ–ª–æ–≤–æ–∫ –ø–∞–∫–µ—Ç–∞ */
        this.parameterHeight=`4vh`; /* –∑–∞–≥–æ–ª–æ–≤–æ–∫ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ */

        this.currentMonth=this.date.getMonth(); /* –º–µ—Å—è—Ü –ø–æ –¥–µ—Ñ–æ–ª—Ç—É –≤–Ω–∞—á–∞–ª–µ */
        this.currentYear=this.date.getFullYear(); /* –≥–æ–¥ –ø–æ –¥–µ—Ñ–æ–ª—Ç—É –≤–Ω–∞—á–∞–ª–µ */
        this.currentPage=currentPage; /* –ø–æ –¥–µ—Ñ–æ–ª—Ç—É —Å—Ç—Ä–∞–Ω–∏—Ü–∞ –∏–∑ –∫–æ–Ω—Ñ–∏–≥–∞ */

        this.numOfRows=Number(monthDuration[this.currentMonth][0]); /* –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç—Ä–æ–∫ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –¥–ª–∏–Ω—ã –º–µ—Å—è—Ü–∞ –±–µ—Ä–µ—Ç—Å—è –∏–∑ –∫–æ–Ω—Ñ–∏–≥–∞ */
        this.numOfColumns=tables[this.currentPage].length*columns.length; /* –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç–æ–ª–±—Ü–æ–≤ –±–µ—Ä–µ—Ç—Å—è –∏–∑ –∫–æ–Ω—Ñ–∏–≥–∞ –¥–ª—è –∫–∞–∂–¥–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã */

        this.clearPage();
        this.createGridStyle();
        this.createTableSimple();
        this.createTableHead();
        this.createTableDates();
    }

    clearPage() {
        this.placeForStylize.innerHTML='';
    }

    createGridStyle() { /* —Ä–∞–∑–º–µ—Ç–∫–∞ –≥—Ä–∏–¥–∞ */

        this.placeForStylize.style.gridTemplateColumns=`[beginDate] ${this.dateWidth} [endDate beginYear] repeat(${this.numOfColumns}, 1fr) [endYear]`;
        this.placeForStylize.style.gridTemplateRows=`
        [beginYear] ${this.yearHeight} [endYear beginPacket] ${this.packetHeight} [endPacket beginParameter]
        ${this.parameterHeight} [endParameter] repeat(${this.numOfRows}, 1fr)
        `;
    }

    createTableSimple() { /* —Å–æ–∑–¥–∞–Ω–∏–µ –º–Ω–æ–∂–µ—Å—Ç–≤–∞ –ø—É—Å—Ç—ã—Ö —è—á–µ–µ–∫ */
        for (let j=1+this.numOfRowsIndent; j <= this.numOfRows+this.numOfRowsIndent; j++) {
            for (let i=this.IndentX; i < this.numOfColumns+this.IndentX; i++) {
                new CreateCell(i,j,``);
            }
        }
    }

    createTableHead(){
        /*** –∑–∞–≥–æ–ª–æ–≤–æ–∫ –≥–æ–¥–∞ ***/
        new CreateCellNamed('beginYear','endYear','beginYear','endYear',
            `${monthDuration[this.currentMonth][2]} ${this.currentYear} –≥–æ–¥–∞`);

        /*** –∑–∞–≥–æ–ª–æ–≤–æ–∫ –ø–∞–∫–µ—Ç–∞ ***/
        tables[this.currentPage].forEach(function(el,ind){

            new CreateCellNamed(ind*2+this.IndentX, ind*2+this.IndentX+2,'beginPacket','endPacket',el,'cellNamedPacket')
        },this);

        /*** –∑–∞–≥–æ–ª–æ–≤–æ–∫ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ ***/
        tables[this.currentPage].forEach(function(el,ind){
            const indexOuter=ind;

            columns.forEach(function(el,ind){
                new CreateCellNamed(ind+this.IndentX+indexOuter*2, ind+this.IndentX+indexOuter*2+1,'beginParameter','endParameter',el,'cellNamedParameter')
            },this,indexOuter)
        },this);

        /*** –∑–∞–≥–æ–ª–æ–≤–æ–∫ –¥–∞—Ç—ã ***/
        new CreateCellNamed(1,2,'beginParameter','endParameter','–î–∞—Ç–∞:','cellNamedDate');

    }

    createTableDates(){/* —Å–æ–∑–¥–∞–Ω–∏–µ –º–Ω–æ–∂–µ—Å—Ç–≤–∞ —è—á–µ–µ–∫ —Å –¥–∞—Ç–∞–º–∏ */
        for (let j=1+this.numOfRowsIndent; j <= this.numOfRows+this.numOfRowsIndent; j++) {

                new CreateCell(1,j,`${j-this.numOfRowsIndent} ${monthDuration[this.currentMonth][1]}`);
        }
    }

    updatePage(page=false,month=0) {

        if (page) {this.currentPage++; if (this.currentPage>=tables.length) {this.currentPage=0}} /* —Ü–∏–∫–ª–∏—á–µ—Å–∫–∞—è —Å–º–µ–Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã */

        this.currentMonth=this.currentMonth+month;
        if (this.currentMonth<0) {this.currentMonth=11; this.currentYear--}; /* –ø–µ—Ä–µ—Ö–æ–¥ –≥–æ–¥–∞ –Ω–∞–∑–∞–¥ */
        if (this.currentMonth>11) {this.currentMonth=0; this.currentYear++}; /* –ø–µ—Ä–µ—Ö–æ–¥ –≥–æ–¥–∞ –≤–ø–µ—Ä–µ–¥ */

        this.updateLayout();
        this.clearPage();
        this.createGridStyle();
        this.createTableSimple();
        this.createTableHead();
        this.createTableDates();
    }

    updateLayout() {
        this.numOfRows=Number(monthDuration[this.currentMonth][0]); /* –ø–µ—Ä–µ—Ä–∞—Å—á–µ—Ç —Å—Ç—Ä–æ–∫ */
        this.numOfColumns=tables[this.currentPage].length*columns.length; /* –ø–µ—Ä–µ—Ä–∞—Å—á–µ—Ç —Å—Ç–æ–ª–±—Ü–æ–≤ */
    }
}

